<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process145"></a>

<p><br></p>
<p><span class="rvts17">主线：拜访回音大厅（</span><span class="rvts50">To the Hall of Echoes</span><span class="rvts17">）</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:634,Y:70</span><span class="rvts21">，地点：回音大厅</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">与回音大厅的神对话</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1090.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">拜访回音大厅，需要找到自己种族所对应的神，并对其施加祝福（</span><span class="rvts20">Bless</span><span class="rvts21">）技能，然后与其对话，便可重返人间。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1091.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">重返人间后，发现船上所有的人都消失了，之后马拉迪在之前关押亚历山大的牢房处等着。与其对话，便会结束该章节，进入下一章节内容。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1092.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">复仇女士号到达死神海岸（</span><span class="rvts20">Reaper's Coast</span><span class="rvts21">），需要乘坐救生船到达岸边开始下一章节的探险。在之后章节可以通过路点传送，再次回到复仇女士号。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



